import { mutate } from "swr";

const API_BASE_URL = (process.env.NEXT_PUBLIC_backend_api_url || "").replace(/\/$/, "");

// small in-memory barcode -> product cache (short TTL)
// now persisted to localStorage and with longer TTL for better UX
type CacheEntry = { product: Product; ts: number };
const BARCODE_CACHE = new Map<string, CacheEntry>();
const CACHE_TTL_MS = 5 * 60 * 1000; // 5 minutes
const STORAGE_KEY = "pos:barcode-cache";

function loadCacheFromStorage() {
  try {
    const raw = typeof window !== "undefined" ? window.localStorage.getItem(STORAGE_KEY) : null;
    if (!raw) return;
    const obj = JSON.parse(raw);
    if (!obj || typeof obj !== "object") return;
    Object.keys(obj).forEach((k) => {
      const entry = obj[k];
      if (entry && entry.product && entry.ts) {
        BARCODE_CACHE.set(k, entry);
      }
    });
  } catch { /* ignore */ }
}

function saveCacheToStorage() {
  try {
    if (typeof window === "undefined") return;
    const obj: Record<string, CacheEntry> = {};
    BARCODE_CACHE.forEach((v, k) => {
      obj[k] = v;
    });
    window.localStorage.setItem(STORAGE_KEY, JSON.stringify(obj));
  } catch { /* ignore */ }
}

function cacheSet(product: Product) {
  if (!product || !product.barcode) return;
  BARCODE_CACHE.set(String(product.barcode), { product, ts: Date.now() });
  // persist (debounced not implemented for simplicity)
  try { saveCacheToStorage(); } catch {}
}

function cacheGet(barcode: string): Product | null {
  if (!barcode) return null;
  const entry = BARCODE_CACHE.get(barcode);
  if (!entry) return null;
  if (Date.now() - entry.ts > CACHE_TTL_MS) {
    BARCODE_CACHE.delete(barcode);
    try { saveCacheToStorage(); } catch {}
    return null;
  }
  return entry.product;
}

// initialize cache from storage (browser only)
if (typeof window !== "undefined") {
  try { loadCacheFromStorage(); } catch {}
}

export const productApi = {
  async getAll(): Promise<Product[]> {
    try {
      const response = await fetch(`${API_BASE_URL}/products`);
      if (!response.ok) {
        return [];
      }
      const json: ApiResponse<Product[]> = await response.json().catch(() => ({ data: [] }));
      return json.data ?? [];
    } catch {
      return [];
    }
  },

  async create(product: Omit<Product, "id">): Promise<Product> {
    const response = await fetch(`${API_BASE_URL}/products`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(product),
    });
    const json: ApiResponse<Product> = await response.json().catch(() => ({ data: null } as any));
    if (!response.ok) {
      const errorMsg = json?.error || json?.message || `Failed to create product: ${response.status}`;
      throw new Error(errorMsg);
    }
    const created = json.data as Product;
    // update local cache so immediate subsequent scans are fast
    try { cacheSet(created); } catch {}

    // optimistic update: add to products list SWR cache (don't revalidate)
    try {
      mutate("/products", (old: any) => {
        if (!old) return [created];
        // if old is object { data: [...] } handle both shapes
        if (Array.isArray(old)) {
          return [created, ...old];
        }
        if (old && Array.isArray(old.data)) {
          return { ...old, data: [created, ...old.data], count: (old.count || old.data.length) + 1 };
        }
        return old;
      }, false).catch(() => {});
    } catch { /* ignore */ }

    return created;
  },

  async update(id: number, product: Omit<Product, "id">): Promise<Product> {
    const response = await fetch(`${API_BASE_URL}/products/${id}`, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(product),
    });
    const json: ApiResponse<Product> = await response.json().catch(() => ({ data: null } as any));
    if (!response.ok) {
      const errorMsg = json?.error || json?.message || `Failed to update product: ${response.status}`;
      throw new Error(errorMsg);
    }
    const updated = json.data as Product;
    try { cacheSet(updated); } catch {}

    // update SWR cache entries for lists and individual product
    try {
      mutate("/products", (old: any) => {
        if (!old) return old;
        if (Array.isArray(old)) {
          return old.map((p) => (p.id === updated.id ? updated : p));
        }
        if (old && Array.isArray(old.data)) {
          return { ...old, data: old.data.map((p) => (p.id === updated.id ? updated : p)) };
        }
        return old;
      }, false).catch(() => {});

      mutate(["product", id], updated, false).catch(() => {});
    } catch { /* ignore */ }

    return updated;
  },

  async getById(id: number): Promise<Product | null> {
    try {
      const response = await fetch(`${API_BASE_URL}/products/${id}`);
      if (!response.ok) {
        if (response.status === 404) return null;
        throw new Error(`Failed to fetch product with id ${id}`);
      }
      const json: ApiResponse<Product> = await response.json().catch(() => ({ data: null } as any));
      return json.data ?? null;
    } catch {
      return null;
    }
  },

  async delete(id: number): Promise<void> {
    const response = await fetch(`${API_BASE_URL}/products/${id}`, {
      method: "DELETE",
    });
    if (!response.ok) {
      const json = await response.json().catch(() => ({}));
      const errorMsg = json?.error || json?.message || `Failed to delete product: ${response.status}`;
      throw new Error(errorMsg);
    }

    // remove from SWR cache lists
    try {
      mutate("/products", (old: any) => {
        if (!old) return old;
        if (Array.isArray(old)) return old.filter((p) => p.id !== id);
        if (old && Array.isArray(old.data)) return { ...old, data: old.data.filter((p) => p.id !== id), count: Math.max(0, (old.count || old.data.length) - 1) };
        return old;
      }, false).catch(() => {});
      mutate(["product", id], null, false).catch(() => {});
    } catch { /* ignore */ }
  },

  async getByBarcode(barcode: string): Promise<Product | null> {
    if (!barcode) return null;

    const clean = (v: string) => String(v).replace(/[\u0000-\u001F\u007F-\u009F]/g, "").trim();
    const cleaned = clean(barcode);

    // check short-lived in-memory cache first
    try {
      const cached = cacheGet(cleaned);
      if (cached) return cached;
    } catch { /* ignore cache errors */ }

    const encoded = encodeURIComponent(cleaned);

    const exactUrl = `${API_BASE_URL}/products/barcode/${encoded}`;
    const queryUrl = `${API_BASE_URL}/products?barcode=${encoded}`;

    // try exact endpoint
    try {
      const res = await fetch(exactUrl);
      if (res.status === 404) return null;
      if (res.ok) {
        const json: ApiResponse<Product> = await res.json().catch(() => ({ data: null } as any));
        const product: Product | null = json?.data ?? null;
        if (product) {
          try { cacheSet(product); } catch {}
          return product;
        }
      }
    } catch {
      // ignore exact endpoint errors
    }

    // fallback to query endpoint
    try {
      const res2 = await fetch(queryUrl);
      if (!res2.ok) return null;
      const qjson: ApiResponse<Product[]> = await res2.json().catch(() => ({ data: [] }));
      const list: Product[] = qjson?.data ?? [];
      if (list.length === 0) return null;
      const exact = list.find(p => clean(p?.barcode ?? "") === cleaned);
      const chosen = exact ?? list[0];
      try { cacheSet(chosen); } catch {}
      return chosen;
    } catch {
      return null;
    }
  },
};
